.repoTitle {
  display: flex;
  align-items: center;
}

.repoTitle path {
  fill: #586069;
}

.title,
.subTitle,
.slash {
  font-size: 18px;
  font-weight: 400;
}

.title,
.subTitle {
  color: #0366d6;
  text-decoration: none;
}

.title {
  margin-left: 8px;
}

.subTitle {
  font-weight: 600;
}

.slash {
  margin: 0 0.25em;
}

.title:hover,
.subTitle:hover {
  text-decoration: underline;
}

@media (min-width: 768px) {
  .repoTitle {
    position: relative;
  }

  .title,
  .subTitle,
  .slash {
    font-size: 20px;
  }
}
